MeteorToysData = {
	'Impersonate': new Mongo.Collection("MeteorToys_Impersonate"),
	'JetSetter':   new Mongo.Collection("MeteorToys_JetSetter"),
	'Mongol':      new Mongo.Collection("MeteorToys_Mongol"),
	'AutoPub':     new Mongo.Collection("MeteorToys_AutoPub"),
	'Email':       new Mongo.Collection("MeteorToys_Email"),
	'Result':      new Mongo.Collection("MeteorToys_Result"),
	'Throttle':    new Mongo.Collection("MeteorToys_Throttle")
}



if (Meteor.isServer) {
	MeteorToysData["credentials"] = new Mongo.Collection("MeteorToysCredentials");
}

if (Meteor.isClient) {
  MeteorToysDict = new ReactiveDict('MeteorToys');
}

MeteorToys_ToyKit = {
  observe: function () {

    // first load
    MeteorToys_ToyKit.runPubSub();
    MeteorToys_ToyKit.runJetSetter();

    // start
    if (!Object.observe) {
      setInterval(function(){
        MeteorToys_ToyKit.runPubSub();
        MeteorToys_ToyKit.runJetSetter();
      }, 3000);
    } else {
      Object.observe(Meteor.default_connection._subscriptions, function() {
        MeteorToys_ToyKit.runPubSub();
      })
      Object.observe(Session.keys, function () {
        MeteorToys_ToyKit.runJetSetter();
      })
    }
    // end
  },
  runJetSetter: function () {
    if (Package["msavin:jetsetter"]) {
      Package["msavin:jetsetter"].JetSetter.getKeys();
    }
  },
  runPubSub: function () {
    if (Package["msavin:sub"] || Package["msavin:mongol"]) {

      var subscriptions  = Meteor.default_connection._subscriptions,
          subKeys        = Object.keys(subscriptions);

      MeteorToysDict.set("MeteorToys_PubSub", subKeys);

    }
  }
}


// Public Function

MeteorToys_JSON = {
  'parse': function (data) {
    var newObject = false;

    try {
      newObject = JSON.parse(data);

    } catch (error) {
      newObject = String(data)
    }

    return newObject;
  },
  'colorize': function (json) {
    // colorized the JSON objects
    if (!json) {
      return "<em>No data</em>";
    }

    if (typeof json != 'string') {
      json = JSON.stringify(json, undefined, 2);
    }

    json = json.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;');

    return json.replace(/("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?)/g, function(match) {
      var cls = 'MeteorToys_number';
      if (/^"/.test(match)) {
        if (/:$/.test(match)) {
          cls = 'MeteorToys_key';
        } else {
          cls = 'MeteorToys_string';
        }
      } else if (/true|false/.test(match)) {
        cls = 'MeteorToys_boolean';
      } else if (/null/.test(match)) {
        cls = 'MeteorToys_null';
      }

      // Return results

      quote = '<span class="' + cls + '">"</span>';
      switch (cls) {
        case "MeteorToys_key":
          match = match.replace(/"/g, "");
          match = match.replace(/:/g, "");
          
          return quote + '<span class="' + cls + '" contenteditable="false">' + match + '</span>' + quote + ':';
          break;
        case "MeteorToys_number": 
          return '<span class="' + cls + '" contenteditable="false">' + match + '</span>';
          break; 
        case "MeteorToys_string":
          match = match.substring(1, match.length-1);
          return quote + '<span class="' + cls + '" contenteditable="false">' + match + '</span>' + quote;
          break;
        case "MeteorToys_boolean":
          return '<span class="' + cls + '" contenteditable="false">' + match + '</span>';
          break;
        case "MeteorToys_null":
          return '<span class="' + cls + '" contenteditable="false">' + match + '</span>';
          break;
      }

      
    });
  },
  'colorizeEditable': function (json) {
    // colorized the JSON objects
    if (!json) {
      return "<em>No data</em>";
    }

    if (typeof json != 'string') {
      json = JSON.stringify(json, undefined, 2);
    }

    json = json.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;');

    return json.replace(/("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?)/g, function(match) {
      var cls = 'MeteorToys_number';
      if (/^"/.test(match)) {
        if (/:$/.test(match)) {
          cls = 'MeteorToys_key';
        } else {
          cls = 'MeteorToys_string';
        }
      } else if (/true|false/.test(match)) {
        cls = 'MeteorToys_boolean';
      } else if (/null/.test(match)) {
        cls = 'MeteorToys_null';
      }

      // Return results

      quote = '<span class="' + cls + '">"</span>';
      switch (cls) {
        case "MeteorToys_key":
          match = match.replace(/"/g, "");
          match = match.replace(/:/g, "");
          
          return quote + '<span class="' + cls + ' MeteorToys_inline" contenteditable="true">' + match + '</span>' + quote + ':';
          break;
        case "MeteorToys_number": 
          return '<span class="' + cls + ' MeteorToys_inline" contenteditable="true">' + match + '</span>';
          break; 
        case "MeteorToys_string":
          match = match.substring(1, match.length-1);
          return '<span class="' + cls + ' MeteorToys_inline" contenteditable="true">"' + match + '"</span>';
          break;
        case "MeteorToys_boolean":
          return '<span class="' + cls + ' MeteorToys_inline" contenteditable="true">' + match + '</span>';
          break;
        case "MeteorToys_null":
          return '<span class="' + cls + ' MeteorToys_inline" contenteditable="true">' + match + '</span>';
          break;
      }

      
    });
  }
}
